export function setFilterStatus(filterStatus){
return ({ type: 'SET_FILTER_STATUS', filterStatus });
}
export function fnaddWord(en,vn){
    const word = {
        _id: Math.random() + '',
       en,
        vn,
        isMemorized: false
    };
    return ({ type: 'ADD_WORD', word });
};
export function fnshouldShowForm(){
    return ({ type: 'TOGGLE_SHOULD_SHOW_FORM' });
};
export function fntoggleShouldShowForm(){
    return ({ type: 'TOGGLE_SHOULD_SHOW_FORM' });
}

export function fnremoveWord(_id){
    return ({ type: 'REMOVE_WORD', _id});
}

export function fntoogleIsMemorized(_id){
    return ({ type: 'TOGGLE_IS_MEMORIZED', _id});

}


