import React from 'react';
import { connect } from 'react-redux';
import * as actionCreator from '../redux/actionCreator';
const WordComponent = ({ wordInfo,fntoogleIsMemorized,fnremoveWord }) => (
    <div className="word" key={wordInfo._id}>
        <div className="word-container">
        <h3 className="text-success">{wordInfo.en}</h3>
        <h3 className="text-danger">
            {wordInfo.isMemorized ? '----' : wordInfo.vn}
        </h3>
        </div>
        <div className="btn-container">
        <button
            className="btn btn-success"
            onClick={() => fntoogleIsMemorized(wordInfo._id)}
        >
            { wordInfo.isMemorized ? 'Forgot' : 'Memorized' }
        </button>
        <button
            className="btn btn-warning" 
            onClick={() => fnremoveWord(wordInfo._id)}
        >
            Remove
        </button>
        </div>
    </div>
);

export const Word = connect(undefined, actionCreator)(WordComponent);



